<template>
	<div class="syfulizhi">
		<p class="pointsMall_title1">
			<i class="icon_pointsMall1"></i>
			<span>当前积分剩余：<b>{{ $store.state.amount }}</b></span>
		</p>
	</div>
</template>

<script>
	export default{
		name:'syfulizhi',
		data() {
			return{
				obj:{}
			}
		},
		mounted() {
			let _this = this;
			var jsonObj = localStorage.getItem("obj");
			_this.obj = JSON.parse(jsonObj);
			_this.getUserLuckyValue();
		},
		methods:{
			getUserLuckyValue(){
				let _this = this;
				var data = _this.byte256.byte256.encode(_this.key, JSON.stringify(_this.obj));
				_this.$axios({
				  method: 'post',
				  url: '/getUserLuckyValue',
				  data:data,
				}).then(function(res) {
				  if(res.data.code === 200){
					var reData = JSON.parse(_this.byte256.byte256.decode(_this.reverse_key, res.data.data));
					_this.userLuckyValue = reData;
					console.log('reData-----',reData);
					console.log('$store-----',_this.$store.state);
					_this.$store.commit('increment',(reData.amount - _this.$store.state.amount));
				
				  }
				});
			},
		}
	}
</script>

<style>
	.syfulizhi{
		
	}
	.pointsMall_title1{
		line-height: 0.9rem;
		height: 0.9rem;
		background-color: #ffe5ed;
		color:#ff588c;
		font-size: 0.4rem;
		box-sizing: border-box;
		padding:0 0.33rem;
	}
	.pointsMall_title1 span{
		display: inline-block;
		vertical-align: middle;
	}
	.icon_pointsMall1{
		display: inline-block;
		width: 0.7rem;
		height: 0.7rem;
		background-image: url(../static/img/icon_welfare.png);
		background-repeat: no-repeat;
		background-size: 100%;
		margin-right: 0.33rem;
		vertical-align: middle;
	}
</style>
