<template>
	<div class="pointsMall">
		<div class="pointsMall_head">
			<i class="icon_arrow_left" @click="goBack"></i>
			<p>积分兑换</p>
			<p class="order" @click="toExchangeRecord"><span>兑换记录</span></p>
		</div>
		<syfulizhi />
		<p class="pointsMall_title2">温馨提示：虚拟商品兑换后不可撤回，请兑换前仔细核对。</p>
		<ul class="commodity_list">
			<li v-for="item in commodityList" :key="item.id" >
				<div class="commodity_list_img" :style="'background-image: url('+item.commodityMainImg+');'"></div>
				<p class="commodity_list_title">{{ item.commodityName }}</p>
				<p class="commodity_list_welfare"><span>{{ item.amount }}</span><span class="act">积分</span></p>
				<p class="commodity_list_btn" @click="toDetails(item)">确认兑换</p>
			</li>
		</ul>
		<div v-if="commodityList.length == 0">
			<van-empty description="暂无商品" />
		</div>
		<div class="pointsMall_popup" v-if="pointsMallPopupFlag">
			<div class="pointsMall_popup_shadow" @click="closePopup"></div>
			<div class="pointsMall_popup_main">
				<p class="pointsMall_popup_title">确认兑换</p>
				<div class="pointsMall_popup_goods">
					<div class="pointsMall_popup_goods_box">
						<img :src="pointsMallPopupItem.commodityMainImg" alt="" class="pointsMall_popup_goods_img"/>
						<div class="pointsMall_popup_goods_box_box">
							<p class="text1">{{ pointsMallPopupItem.commodityName }}</p>
							<p class="text2">{{ pointsMallPopupItem.amount }}积分</p>
						</div>
					</div>
					<p class="pointsMall_popup_goods_conut">X1</p>
				</div>
				<p class="pointsMall_popup_money">
					<span class="text1">实际支付：</span>
					<span class="text2">{{ pointsMallPopupItem.amount }}积分</span>
				</p>
				<p class="pointsMall_popup_tips">虚拟用品不可退款哦，兑换后可直接使用</p>
				<img src="../static/img/btn_pointsMall_popup.png" alt="" class="btn_pointsMall_popup" @click="exchangeAddOrder"/>
			</div>
		</div>
	</div>
</template>

<script>
	import syfulizhi from '../components/syfulizhi.vue'
	export default{
		name:'pointsMall',
		components:{
			syfulizhi
		},
		data(){
			return {
				pointsMallPopupFlag:false,
				commodityList:[],
				pointsMallPopupItem:{}
			}
		},
		mounted() {
			let _this = this;
			var jsonObj = localStorage.getItem("obj");
			_this.obj = JSON.parse(jsonObj);
			_this.exchangeCommodityList();
		},
		methods:{
			goBack(){
				this.$router.go(-1);
			},
			toDetails(item){
				let _this = this;
				_this.pointsMallPopupItem = item;
				_this.pointsMallPopupFlag = true;
				// this.$router.push({path:'/pointsDetails',query:{item:item}})
			},
			toExchangeRecord(){
				this.$router.push({path:'/exchangeRecord'})
			},
			exchangeCommodityList(){
				let _this = this;
				var data = _this.byte256.byte256.encode(_this.key, JSON.stringify(_this.obj));
				_this.$axios({
				  method: 'post',
				  url: '/exchange/commodityList',
				  data:data,
				}).then(function(res) {
				  if(res.data.code === 200){
					var reData = JSON.parse(_this.byte256.byte256.decode(_this.reverse_key, res.data.data));
					console.log(reData);
					_this.commodityList = reData;
				  }
				});
			},
			exchangeAddOrder(){
				let _this = this;
				var exclusiveObj = {
					commodityId:_this.pointsMallPopupItem.id
				};
				var requestData = Object.assign(_this.obj,exclusiveObj);
				var redata = _this.byte256.byte256.encode(_this.key, JSON.stringify(requestData));
				_this.$axios({
				  method: 'post',
				  url: '/exchange/addOrder',
				  data:redata,
				}).then(function(res) {
					console.log('addOrder=====',res);
				  if(res.data.code === 200){
					_this.pointsMallPopupFlag = false;
					_this.$toast('兑换成功');
				  }
				});
			},
			closePopup(){
				this.pointsMallPopupFlag = false;
			}
		}
	}
</script>

